.main-navbar {
  display: flex;

  justify-content: space-between;
  align-items: center;
  padding: 10px 40px 10px;
  height: 60px;
  background: #090809;

  ul {
    font-weight: 600;
    display: flex;
    flex-direction: row;
    gap: 40px;
    list-style-type: none;
    align-items: center;
    a {
      text-decoration: none;
      padding: 10px;
      border-radius: 6px;
      color: #473949;
    }
    a:hover {
      color: rgb(255, 255, 255);
      background-color: #61216e52;
    }
  }
}
