.popularList {
  display: flex;
  justify-content: flex-start;
  overflow: scroll;
  overflow-y: hidden; /* Hide vertical scrollbar */
  gap: 20px;
  width: 100%;
  height: 160px;
  &Section h2 {
    margin: 20px 0 0;
    font-size: 20px;
    text-transform: uppercase;
  }
  &Card {
    &__text {
      padding: 0 30px;
      h3 {
        margin: 0;
        position: relative;
        text-transform: uppercase;
        z-index: 1;
      }
      p {
        position: relative;
        font-size: 0.9rem;
        font-weight: bold;
        z-index: 1;
      }
      p::before {
        content: "Average: ";
        opacity: 0.7;
      }
    }
  }
}
