.MainInput {
  height: 40px;
  display: flex;
  gap: 8px;

  border-radius: 8px;
  transition: all 0.35s;
  input {
    padding-left: 14px;
    width: 240px;
    border: 0;
    outline: none;
    font-weight: bold;
    border-radius: 8px;
  }
  button {
    width: 100px;
    border: 0;
    font-weight: bold;
    background-color: white;

    cursor: pointer;

    border-radius: 8px;
  }
}
